.sidebar {
    height: 0px;
    overflow: hidden;
}

.sidebar.open {
    display: flex;
    flex-direction: row-reverse;
    position: absolute;
    z-index: 100;
    width: 250px;
    height: 100%;
    background: #FFFFFF;
    box-shadow: 0px 0px 40px rgba(29, 33, 38, 0.13);

}

.background {
    background-color: rgba(0, 0, 0, .3);
    height: 100%;
    position: fixed;
    width: 100%;
}

.nav {
    background: #FFFFFF;
    padding-left: 20px;
}

.nav a {
    display: flex;
    justify-content: space-between;
    padding-top: 20px;
    /*margin: 10px 0 10px 70px;*/
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 22px;
    line-height: 27px;
    text-decoration-line: none;
    color: #000000;

}

.nav a:hover {
    text-decoration-line: none;
    color: #58ACFF;
}

.nav a.active {
    text-decoration-line: underline;
    color: #0059B2;
}

.close {
    display: flex;
    justify-content: center;
    width: 24px;
    height: 24px;
    border: none;
    background: none;
    margin-top: 16px;
    margin-right: 24px;
}

.close img {
    width: 20px;
    height: 20px;
}